/* setting base font sizes */

html {
  font-size: 62.5%;
  font-family: 'Raleway', sans-serif;

}

/* setting wrapper width */

body {
  background: url('../src/assets/background2.jpg');
  min-height: 95vh;
  
  background-size: contain;
  background-repeat: repeat;

  /* position: relative; */
}

.wrapper {
  max-width:90%;
  margin: 0 auto;
}

.displayForm {
  background: #181307;
  padding: 2rem;
  border-radius: 1rem ;
  margin-top: 2rem;
}

/* headline styles  */
.headLine {
  text-align: center;
  font-family: 'Gayathri', sans-serif;
  text-transform: uppercase;
  letter-spacing: 1rem;
  font-size: 5rem;
  color: aliceblue;
}

/* search form styles */
.searchForm{
  display: flex;
  flex-flow: column wrap;
  align-items: center;

}


/* search form input styles */
.searchForm input {
  width: 70%;
  height: 3rem;
  border: none;
  border-bottom: 0.5rem solid aliceblue;
  padding: 2rem 0;
  margin: 1rem;
  font-size: 1.5rem;
  text-align: center;
  background: transparent;
  transition: 0.3s ease;
  color: yellow;
 
 

}

.formLabel {
  color: aliceblue;
  font-size:2rem;
  position: relative;
  left: 0;
  top: -5rem;
  transition: 0.25s ease;
}

/* focus state for search form input */
input:focus {
  border-bottom: 0.3rem solid yellow;
}

input:focus + .formLabel, input:valid + .formLabel {
  color: yellow;
  margin: 0;
  border: none;
  font-size: 1rem;
  top: -7rem;
  left: 0;
}




/* search button styles */
.searchForm button {
  color: black;
  background: white;
  border: 0.3rem solid black;
  padding: 2rem;
  text-transform: uppercase;
  font-size: 1.5rem;
  font-weight: bold;
  letter-spacing: 0.5rem;
  text-align: center;
  margin-top: 2rem;
  cursor: pointer;
}


/* hover and focus states for search button */
.searchForm button:hover,
.searchForm button.focus {
  background:yellow;
  color: black;
  
}

/* results display div styling  */
.resultsDisplay {
  background: white;
  border: 0.1rem solid black;
  border-radius: 1rem;
  margin-top: 2rem;
  padding: 3rem 10rem;
}

/* main head for individual search result */
.resultsDisplay h3 {
  text-align: center;
  font-size: 2.5rem;
  text-transform: capitalize;
  padding-top: 2rem;
}


/* text for individual search result */

.resultsDisplay p {
  font-size: 1.5rem;
  text-align: center;
  font-style: italic;
}


/* image and ingredient list flex */
.flexContainer {
  display: flex;
  flex-flow: row nowrap;
  justify-content: space-between;
}


/* image styles */

.flexContainer img {
  border: 0.3rem solid black; 
  margin: 2rem 4rem;
  border-radius: 1%;
  width: 50%;
  height: 50%;
  
}


.ingredientList {
  width: 50%;
}

.flexContainer ul {
  font-size: 2rem;
  
}

.flexContainer ul li {
  margin: 0.5rem 0;
}


/* styles for buttons in search results */
.resultsDisplay a {
  padding: 1rem;
  border: 0.2rem solid black;
  background: black;
  color: white;
  text-decoration: none;
  text-align: center;
  display: block;
  width: 20%;
  margin: auto;
  text-transform: uppercase;
  border-radius: 2%;
  border: 0.1rem solid white;
}

a.topButton{
  padding: 1rem;
  border: 0.2rem solid black;
  background: black;
  color: white;
  text-decoration: none;
  text-align: center;
  display: block;
  width: 20%;
  margin: auto;
  text-transform: uppercase;
  border-radius: 2%;
  border: 0.1rem solid white;
}


/* hover and focus states for buttons in search results */
.resultsDisplay a:hover,
.resultsDisplay a:focus,
a.topButton:hover,
a.topButton:focus {
  background: white;
  color: black;
}


/* status of results text styles */
.statusText {
  text-align: center;
  font-family: 'Raleway', sans-serif;
  font-size: 3rem;
  margin-top: 4rem;
  text-transform: capitalize;
  
  color: white;
  
}

.errorHandle {
  background: #181307;
  padding: 2rem;
  border-radius: 1rem ;
  margin-top: 2rem;
}




/* media queries here */


/* Styles for medium to large screens added here */

@media all and (min-width:750px) and (max-width:1000px) {
  html {
    font-size: 55%;
  }

  
    
  .resultsDisplay {
    margin: 1rem;
    padding: 1rem;
  }
  .flexContainer {
    flex-flow: column nowrap;
  }

  .flexContainer img,
  .ingredientList {
    width: 100%;
    margin: 2rem auto;
  }

  .resultsDisplay a,
  a.topButton {
    font-size: 1.5rem;
    width: 40%;
  }
}


